import { useEffect } from "react";
import pageDataStore from "stores/pageDataStore";
import classes from "./services.module.scss";
import { observer } from "mobx-react-lite";

const ServicesPage = () => {
  const { getServices, services } = pageDataStore;

  useEffect(() => {
    getServices();
  }, []);

  const getDataSides = (order_list) => {
    const middleIndex = Math.ceil(order_list?.length / 2);
    const leftItems = order_list?.slice(0, middleIndex);
    const rightItems = order_list?.slice(middleIndex);

    return { leftItems, rightItems };
  };

  return (
    <div className={classes.servicesWrapper}>
      <div className="container rowGap-30">
        <div className={`${classes.headerContent} fitContent`}>
          <h4 className="title">Our Services</h4>
        </div>
        <div className={`${classes.servicesBlock} rowGap-60`}>
          {services?.map((service, i) => {
            const order_list = service.order_list;

            const isGrouped = order_list?.[0]?.sub_title !== undefined;

            return (
              <div key={i} className={`${classes.service} rowGap-70`}>
                <div className="text-center rowGap-20">
                  <h5 className="paragraph-black size-25 fw-normal">
                    {service.title}
                  </h5>
                  <p className="paragraph-black size-20">
                    {service.main_description}
                  </p>
                </div>

                <div className={classes.serviceWrapper}>
                  {isGrouped
                    ? order_list.map((group, gi) => {
                        const { leftItems, rightItems } = getDataSides(
                          group.data
                        );
                        return (
                          <div key={gi} className="rowGap-20">
                            <h5 className="paragraph-black size-17 text-center">
                              {group.sub_title}
                            </h5>
                            <div className={classes.serviceBody}>
                              <div
                                className={`${classes.column} ${classes.left} rowGap-20`}
                              >
                                {leftItems.map((item, index) => (
                                  <div key={index} className="rowGap-10">
                                    <h5 className="size-26">{item.title}</h5>
                                    {item.description && (
                                      <p className="paragraph-black size-18">
                                        {item.description}
                                      </p>
                                    )}
                                  </div>
                                ))}
                              </div>
                              <div
                                className={`${classes.column} ${classes.right} rowGap-20`}
                              >
                                {rightItems.map((item, index) => (
                                  <div key={index} className="rowGap-10">
                                    <h5 className="size-28">{item.title}</h5>
                                    {item.description && (
                                      <p className="paragraph-black size-20">
                                        {item.description}
                                      </p>
                                    )}
                                  </div>
                                ))}
                              </div>
                            </div>
                          </div>
                        );
                      })
                    : (() => {
                        const { leftItems, rightItems } =
                          getDataSides(order_list);
                        return (
                          <div className={classes.serviceBody}>
                            <div
                              className={`${classes.column} ${classes.left} rowGap-20`}
                            >
                              {leftItems.map((item, index) => (
                                <div key={index} className="rowGap-10">
                                  <h5 className="size-26">{item.title}</h5>
                                  <p className="paragraph-black size-18">
                                    {item.description}
                                  </p>
                                </div>
                              ))}
                            </div>
                            <div
                              className={`${classes.column} ${classes.right} rowGap-20`}
                            >
                              {rightItems.map((item, index) => (
                                <div key={index} className="rowGap-10">
                                  <h5 className="size-28">{item.title}</h5>
                                  <p className="paragraph-black size-20">
                                    {item.description}
                                  </p>
                                </div>
                              ))}
                            </div>
                          </div>
                        );
                      })()}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default observer(ServicesPage);
